import React from 'react';
import PropTypes from 'prop-types';

import ProductTile from './../ProductTile'

const ProductContainer = props => {

  const productTiles = props.products && props.products.length !== 0 && props.products.map((product, key) => {
    const discount =  product.discount && product.price * (product.discount/100)
    product.discountedPrice = product.price - discount
    return <ProductTile product={product} key={key} />
  })

  return (
    <main className='c-Plp__c-ProductContainer'>
      <div className="container">
        <div className="row">
          {productTiles}
        </div>
      </div>
    </main>
  );
};

ProductContainer.defaultProps = {
  products: []
};

ProductContainer.propTypes = {
  products: PropTypes.array
};

export default ProductContainer;